@media all and (max-width: 821px){
    .menu{

        .menu-hamburguesa {
            display: inline-flex;

        }
        
        &__list__black{

            & a {

                display: none;
            }
        }
        &__list{

            & a {

                display: none;
            }
        }
    }

    .cart {
        display: block;

        &__total {
            margin-left: 200px;
            margin-top: 50px;

            &__btn {
                margin-left: 0 auto;
                margin-right: 0 auto;
                margin-top: 16px;
            }

            &__info {
                height: 114px;

                & h1 {
                    font-size: 2.8em;
                }
            }
        }

        &__row{
        
            width: 100%;
            height: 100%;
            overflow: hidden;

             &__separator{
                width: 570px;
                margin-left: 140px;
             }
     
             &__second{
                 display: flex;
                 margin-left: 317px;

                 & p {
                     margin-right: 80px;
                 }

            }

        }
        &__resume {
            height: 100%;

            .product_img {
                width: 130px;
            }

            & li {

                & div {
                    margin-right: 55px;
                }

                .product__price{
                    margin-right: 24px;
                }
            }
        }
    }
    .form{
        .product{
            display: block;
            margin-left: 220px;
        }
        &__tittle{
            font-size: 3.2em;
        }
        
        .separator{
            width: 500px;
        }

        .submit{
            position: absolute;
            bottom: 3px;
            width: 338px;
        }
        
        .description__left{
            margin-top: -40px;
        }
        .description__right{
            width: 338px;
            height: 85px;
            border-radius: 20px;
        }
        
    }
    .shop{
        .shop__filters{
            margin-right: 0;
            margin-left: 248px;
        }

        .filters__right{
            position: absolute;
            bottom: 785px;
            transform: translateX(92%);
        }

        &__separator{
            width: 566px;
        }

        .products{
            padding-left: 50px;
        }
    }
    .detail{
        display: block;

        &__photo{
            width: 100%;
            height: 70%;
        }

        .detail__header{
            & img {
                display: none;
            }
            .menu__list__black{
                position: absolute;
                top: 48px;
                right: 0;
            }
        }

        &__info{
            width: 100%;
            height: 30%;

            .detail__text{
                width: 100%;
                font-size: 5em;
                overflow: hidden;
                margin-top: 40px;

                &__02{
                    display: none;
                }

                &__03{
                    display: none;
                }

                & h1{
                    font-size: 1em;
                    width: 100%;
                    margin-left: 50px;
                }
            }
    
            .detail__info{
                width: 100%;
                overflow: hidden;
                margin-top: 0px;
    
                & h3{
                    font-size: 1.8em;
                    margin-left: 50px;
                    position: absolute;
                    right: 80px;
                    top: 79%;
                }
    
                & p{
                    margin-left: 50px;
                }
            }
    
            .detail__btn{
                display: flex;
                margin-top: -10px;
                margin-left: 50px;
    
                .btn__cart{
                    width: 80%;
                    height: 38px;
                    cursor: pointer;
                    background-color: $blue-maog;
                    border-radius: 54px;
                    border: none;
                    outline: none;
                    font-family: 'Visby CF';
                    color: $white-maog;
                    font-size: 1em;
                    margin-left: 24px;
                }

                & img {
                    display: none;
                }
            }
        }
    }

}

